<template>
  <div>
    <el-header style="position: relative">
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane label="全部会议" name="first">
          <div class="meeting-list">
            <div class="meeting-title">会议列表</div>
            <el-divider></el-divider>
            <div
              v-for="meeting in meetingList"
              :key="meeting.meetingId"
              class="meeting-item"
              @click="enterMeeting(meeting.meetingId)"
            >
              <h3 class="meeting-name">{{meeting.title}}</h3>
               <div class="meeting-long">会议时长:{{meeting.endTime-meeting.startTime |dataFilter2}}</div>
              <div class="meeting-owner">发起人:{{meeting.ownerName}}</div>
              <div class="meeting-time">
                <div style="margin-right:10px">开始时间:{{meeting.startTime|dateFormat}}</div>
                <div>结束时间:{{meeting.endTime|dateFormat}}</div>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="历史会议" name="history">
          <div class="meeting-list">
            <div class="meeting-title">历史会议</div>
            <el-divider></el-divider>
            <div
              v-for="meeting in historyMeetingList"
              :key="meeting.meetingId"
              class="meeting-item"
              @click="enterMeeting(meeting.meetingId)"
            >
              <h3 class="meeting-name">{{meeting.title}}</h3>
               <div class="meeting-long">会议时长:{{meeting.endTime-meeting.startTime |dataFilter2}}</div>
              <div class="meeting-owner">发起人:{{meeting.ownerName}}</div>
              <div class="meeting-time">
                <div style="margin-right:10px">开始时间:{{meeting.startTime|dateFormat}}</div>
                <div>结束时间:{{meeting.endTime|dateFormat}}</div>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="待办列表" name="todo">
          <div class="meeting-list">
            <div class="meeting-title">待办列表</div>
            <el-divider></el-divider>
            <div
              v-for="meeting in todoMeetingList"
              :key="meeting.meetingId"
              class="meeting-item"
              @click="enterMeeting(meeting.meetingId)"
            >
              <h3 class="meeting-name">{{meeting.title}}</h3>
               <div class="meeting-long">会议时长:{{meeting.endTime-meeting.startTime |dataFilter2}}</div>
              <div class="meeting-owner">发起人:{{meeting.userName}}</div>
              <div class="meeting-time">
                <div style="margin-right:10px">开始时间:{{meeting.startTime|dateFormat}}</div>
                <div>结束时间:{{meeting.endTime|dateFormat}}</div>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="我的会议" name="mine">
          <div class="meeting-list">
            <div class="meeting-title">我的会议</div>
            <el-divider></el-divider>
            <div
              v-for="meeting in mineMeetingList"
              :key="meeting.meetingId"
              class="meeting-item"
              @click="enterMeeting(meeting.meetingId)"
            >
              <h3 class="meeting-name">{{meeting.meetingName}}</h3>
               <div class="meeting-long">会议时长:{{meeting.duration  |dataFilter2}}</div>
              <div class="meeting-owner">发起人:{{meeting.userName}}</div>
              <div class="meeting-time">
                <div >开始时间:{{meeting.time|dateFormat}}</div>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>

      <el-input
        placeholder="查找会议"
        v-model="meetingInput"
        prefix-icon="el-icon-search"
        class="search-meeting"
      >
        <el-button slot="append" @click="searchMeeting">搜索</el-button>
      </el-input>
      <el-button type="text" @click="showCreateMeetingVisible = true" class="create-meeting">创建会议</el-button>
    </el-header>
    <el-dialog
      title="创建会议"
      :visible.sync="showCreateMeetingVisible"
      width="40%"
      @close="CreateMeetingClose"
    >
      <el-form :model="meeting" label-width="120px" label-suffix=":" label-position="left">
        <el-form-item label="会议名称">
          <el-input v-model="meeting.title"></el-input>
        </el-form-item>
        <el-form-item label="所属 projectId">
          <el-input v-model="meeting.belongTo" disabled></el-input>
        </el-form-item>
        <el-form-item label="开始时间">
          <el-date-picker
            v-model="meeting.startTime"
            type="datetime"
            placeholder="选择开始时间"
            value-format="timestamp"
            prefix-icon="el-icon-caret-bottom"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="预计结束时间">
          <el-date-picker
            v-model="meeting.endTime"
            type="datetime"
            placeholder="选择结束时间"
            value-format="timestamp"
            prefix-icon="el-icon-caret-bottom"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="note">
          <el-input v-model="meeting.note"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createMeeting">创建会议</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Meeting",
  data() {
    return {
      todoMeetingList: [],
      meetingList: [],
      historyMeetingList: [],
      mineMeetingList: [],
      activeName: "first",
      meetingInput: "",
      projectId: "",
      showCreateMeetingVisible: false,
      meeting: {
        title: "前端代码整合",
        belongTo: "",
        startTime: "",
        endTime: "",
        note: "太难了"
      }
    };
  },
  methods: {
    CreateMeetingClose() {
      this.meeting.title = "";
      this.meeting.startTime = "";
      this.meeting.endTime = "";
      this.meeting.note = "";
    },
    async createMeeting() {
      const { data: res } = await this.$http.post("meeting/", this.meeting);
      if (res.result === 1) {
        this.getMeeting(this.projectId);
        this.activeName = "first";
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
      this.showCreateMeetingVisible = false;
    },
    async searchMeeting() {
      const { data: res } = await this.$http
        .post(`meeting/search/`, {
          linkNum: this.meetingInput
        })
        .catch(function(error) {
          console.log(error);
        });

      if (res.result == 1) {
        this.meetingList = res.data.meetingList;
        console.log(res);
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    async getMeetingByName(name) {
      switch (name) {
        case "first":
          this.getMeeting(this.projectId);
          break;
        case "todo":
          this.getTodoMeeting();
          break;
        case "mine":
          this.getMineMeeting();
          break;
        case "history":
          this.getHistoryMeeting();
          break;
      }
    },
    async getTodoMeeting() {
      const { data: res } = await this.$http
        .get(`meeting/todo`)
        .catch(error => console.log(error));
      if (res.result == 1) {
        this.todoMeetingList = res.data.meetingList;
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    async getHistoryMeeting() {
      const { data: res } = await this.$http
        .get(`meeting/history`)
        .catch(error => console.log(error));
      if (res.result == 1) {
        this.histroyMeetingList = res.data;
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    async getMineMeeting() {
      const { data: res } = await this.$http
        .get(`meeting/mine`)
        .catch(error => console.log(error));
      if (res.result == 1) {
        this.mineMeetingList = res.data.meetingList;
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    async enterMeeting(id) {
      //路由跳转
    },
    handleClick(tab, event) {
      this.getMeetingByName(tab.name);
    },
    async getMeeting(project) {
      const { data: res } = await this.$http
        .get(`project/${project}/getMeeting`)
        .catch(function(error) {
          console.log(error);
        });
      if (res.result == 1) {
        this.$message({
          message: "获取会议列表",
          type: "success"
        });
        this.meetingList = res.data;
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    }
  },
  mounted() {
    this.token = window.sessionStorage.getItem("token");
    this.projectId = this.$route.params.id;
    this.meeting.belongTo = this.$route.params.id;
    console.log(this.projectId);
    this.getMeeting(this.projectId);
  }
};
</script>

<style scoped>

.meeting-list {
  width: 80%;
  display: flex;
  flex-direction: column;
  margin-left: 10%;
}
.meeting-title {
  background-color: rgba(34, 103, 253, 0.39);
  height: 42px;
  margin-bottom: -10px;
  line-height: 42px;
  font-size: 18px;
  padding-left: 10px;
  border-radius: 6px;
}
.meeting-item {
  width: 100%;
  position: relative;
  height: 80px;
  padding: 10px;
  background-color: rgba(0, 59, 252, 0.062);
  margin-bottom: 10px;
  border-radius: 6px;
}
.meeting-name {
  position: absolute;
  top: 10px;
  left: 25px;
  font-size: 16px;
}
.meeting-owner {
  position: absolute;
  bottom: 16px;
  left: 50px;
}
.meeting-long{
  position: absolute;
  top: 10px;
  right: 20px;
}
.meeting-time {
  display: flex;
  position: absolute;
  bottom: 10px;
  right: 20px;
}
.search-meeting {
  width: 300px;
  position: absolute;
  top: 2px;
  left: 450px;
}
.create-meeting {
  position: absolute;
  left: 780px;
  top: 2px;
}
</style>



